body {
    margin:0;
    padding:0;
}
body{
  color:#f5f9f5;
  font-size: 14px;
  line-height:150%;
}

a {
  text-decoration: none;
  color:#006fd7;
}

.footer {
      font-family: Roboto;
      background-color:#001529;  
      display:grid;
      grid-template-columns: 1fr 1fr 1fr;
  }

  .help-and-support {
    margin:20px;
    grid-column-start: 1;
    grid-column-end: 2;
  }
  .content {
    color:#006fd7;
    display: inline;
  }
  .footer .quick-link {
    margin:20px;
    grid-column-start: 3;
    grid-column-end: 4;
  }
 
body {
    margin:0;
    background-color: white;
}

div.signInFormContainer {
    height:100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    grid-template-columns: 1fr 1fr 1fr;
}
    
input[type=text], input[type=password] {
    width:100%;
    padding:12px 20px;
    margin:8px 0;
    display:inline-block;
    border:1px solid #ccc;
    box-sizing: border-box;
}

button {
    background-color:#006fd7;
    color:white;
    padding: 14px 20px;
    margin:8px 0;
    border:none;
    cursor: pointer;
    width:100%;
}

button:hover {
    opacity:0.8;
}


